"use client";

import React, { useState } from "react";
import levelsData from "../module/data/modules"; // 🔹 Importamos los datos de los módulos
import ModuleCard from "../../progress/components/ModuleCard";
import UserProgress from "../../progress/components/UserProgress";
import ExamButton from "../../progress/components/ExamButton";
import LevelCompletionModal from "../../progress/components/LevelCompletionModal";
import ExamModal from "../../progress/components/ExamModal"; // 🔹 Importamos el modal del examen

const ProgressPage: React.FC = () => {
  const [showModal, setShowModal] = useState(false);
  const [isExamOpen, setIsExamOpen] = useState(false);
  const [currentLevel, setCurrentLevel] = useState<"Principiante" | "Intermedio" | "Avanzado">("Principiante");
  const [isExamCompleted, setIsExamCompleted] = useState(false);
  const [levels, setLevels] = useState(levelsData ?? []); // 🔹 Evita que `levels` sea `undefined`
  const [currentModuleId, setCurrentModuleId] = useState<number | null>(null); // Para saber qué módulo se está evaluando

  // 🔹 Desbloquea el siguiente módulo si el usuario aprueba el examen
  const unlockNextModule = (moduleId: number) => {
    setLevels((prevLevels) =>
      prevLevels.map((level) => ({
        ...level,
        modules: level.modules.map((module) => {
          if (module.id === moduleId) {
            return { ...module, status: "completed" }; // Marca el actual como completado
          }
          if (module.id === moduleId + 1 && module.status === "locked") {
            return { ...module, status: "active" }; // Desbloquea el siguiente módulo
          }
          return module;
        }),
      }))
    );
  };

  // 🔹 Se ejecuta cuando el usuario aprueba un examen
  const handleExamPassed = () => {
    if (currentModuleId !== null) {
      unlockNextModule(currentModuleId);
      setShowModal(true);
      setIsExamCompleted(true);
      setIsExamOpen(false);
    }
  };

  return (
    <div className="p-6">
      <UserProgress
        username="Camila Gómez"
        levels={[
          { id: 1, label: "Principiante", completedIcon: "/assets/level-1-completed.png", lockedIcon: undefined, state: "completed" },
          { id: 2, label: "Intermedio", completedIcon: "/assets/level-2-completed.png", lockedIcon: "/assets/level-2-locked.png", state: "in-progress" },
          { id: 3, label: "Avanzado", completedIcon: "/assets/level-3-completed.png", lockedIcon: "/assets/level-3-locked.png", state: "locked" },
        ]}
      />

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {levels.map((level) => {
          const allModulesCompleted = level.modules.every((module) => module.status === "completed");

          return (
            <div key={level.id} className="flex flex-col gap-4">
              <h2 className="text-lg font-bold text-primary P16">{level.title}</h2>
              <p className="text-sm text-neutral-600">{level.subtitle}</p>

              <div className="flex flex-col gap-2">
                {level.modules.map((module) => (
                  <ModuleCard
                  key={module.id}
                  title={module.title}
                  status={module?.status ?? "locked"}
                  href={module.status !== "locked" ? `/progress/module/${module.id}` : undefined}
                />
                
                
                ))}
              </div>

              <div className="mt-6 flex flex-col items-center">
                <p className="p16 text-neutral-600 mb-4">
                  {allModulesCompleted ? `Avanza al examen del nivel ${level.title}` : `Inicia el examen del nivel ${level.title}`}
                </p>
                <ExamButton isDisabled={!allModulesCompleted || isExamCompleted} onClick={() => {
                  setCurrentModuleId(level.modules[0]?.id ?? null); // 🔹 Verificamos que exista el módulo
                  setIsExamOpen(true); // Abrimos el modal
                }}>
                  Comenzar examen
                </ExamButton>
              </div>
            </div>
          );
        })}
      </div>

      {showModal && <LevelCompletionModal level={currentLevel} username="Camila Gómez" onClose={() => setShowModal(false)} />}

      {isExamOpen && <ExamModal isOpen={isExamOpen} onClose={() => setIsExamOpen(false)} moduleId={currentModuleId ?? 0} onExamPassed={handleExamPassed} />}
    </div>
  );
};

export default ProgressPage;
